.App {
  text-align: center;
  margin-left: 60px;
  margin-right: 60px;
  margin-top: 20px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

#map {
  width: 70%;
  margin-top: 40px;
  height: 650px;
  float: right;
  border-style: solid;
}

h1 {
  font-size: 45px;
  margin-bottom: 0px;
  margin-top: -5px;
  font-family: futura;
}

.p-contain h4 {
  font-size: 16px;
  color: green;
}

.p-contain h2, h3, h4 {
  margin-left: 50px;
}


.p-contain {
  padding-right: 50px;
  margin-top: 40px;
  color: black;
  float: right;
  width: 24%;
  height: 656px;
  font-size: 14px;
  overflow-y:scroll;
  margin-right: 10px;
  text-align: left;
}

.place {
  border-style: solid;
  background-color: #A9CCE3;
  font-family: futura;
}

::-webkit-scrollbar { 
    display: none; 
}

.save, .pure-button {
  height: 40px;
  width: 150px;
  border: 2px solid;
  margin: 10px 10px 10px 20px;
  color: black;
  font-weight: bold;
  text-transform: uppercase;
  background-color:none;
  text-decoration: none;
  background-color: transparent;
  font-size: .8em;
  letter-spacing: 1.5px;
}

.save:hover, .pure-button:hover {
  border: 5px solid;
}

body:after {
  background-image: url("onebr1.png");
  content : "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  opacity : 0.8;
  z-index: -1;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.f-input::-webkit-input-placeholder {
  color:black;
  opacity:0.9;
}

.f-input:-ms-input-placeholder {
  color:black;
  opacity:0.9;
}

.f-input::placeholder {
  color:black;
  opacity:0.9;
}

.f-input {
  background-color: transparent;
  border:2px solid black;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  padding: 13px;
  width:10%;
  height: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color:black;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 18px;
  border-radius: 4px;
  line-height: 15px;
  text-align:left;
  margin-right: 20px;
  margin-left: 10px;
}

.f-input:focus { 
border:2px solid #3a3939; }

textarea {
  height: 250px;
  line-height: 150%;
  resize:vertical;
}

.price-t {
  background-color: transparent;
  border:2px solid black;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  padding: 13px;
  width:10%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color:black;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 18px;
  border-radius: 4px;
  line-height: 15px;
  text-align:left;
   margin-right: 20px;
   margin-left: 10px;

 }






